  max-width: 126px;
}

.wizard-settings table td .values:before {
  content: '';
  display: block;
}

.wizard-settings input,
.wizard-settings select {
  width: 126px;
}

.wizard-settings input {
  padding: 0px 4px;
}

.wizard-settings input[disabled] {
  color: #808080;
  background: #a7a7a7;
  border: 1px solid #a7a7a7;
}

.preview {
  width: 70%;
  float: left;
}

@media (min-width: 0px) {
  .preview {
    width: 100%;
    float: none;
  }
}

@media (min-width: 1024px) {
  //.preview {
  //  width: calc(100% - 340px);
  //}

  .wizard-settings {
    top: 0;
    min-height: 100%;
  }

  .wizard-settings .inner {
    margin-top: 60px;
  }

  .wizard-settings .footer {
    position: absolute;
    bottom: 0;
    left: 0;
  }

  .wizard-settings .settings-controls {
    position: fixed;
    top: 0;
    right: 0;
    width: 340px;
    padding: 10px 0 0;
    text-align: center;
    background-color: inherit;
  }
}

@media screen and (max-width: 1024px) {
  .api-controls button, .api-controls button:active, .api-controls button:focus {
    width: 70px;
  }
}

@media screen and (max-width: 1023px) {
  .app-figure {
    width: 98% !important;
    margin: 50px auto;
    padding: 0;
  }

  .app-code-sample {
    display: none;
  }

  .wizard-settings {
    width: 100%;
  }
}

