//COLORS
$border-hover: #BCCBD9;


//Methods
$browser-context: 16;
@function rem($pixels, $context: $browser-context) {
  @return #{$pixels/$context}rem;
}

// fonts all big

@for $i from 1 through 6 {
  @media(min-width: 1920px + ($i * 100)) {
    html {
      font-size: 16px + $i !important;
    }
  }
}

//BOOTSTRAP CUSTOM GRID CONTAINER

@media (min-width: 1200px) {
  .container, .container-lg, .container-md, .container-sm, .container-xl {
    max-width: 1170px;
  }
}


@media (min-width: 1600px) {
  .container, .container-lg, .container-md, .container-sm, .container-xl {
    max-width: 80%;
  }
}


@media (min-width: 2560px) {
  .container, .container-lg, .container-md, .container-sm, .container-xl {
    max-width: 1800px;
  }
}

//CLASSES
h2 {
  color: #000000;
  font-size: 40px;
  font-weight: 400;
  text-transform: uppercase;
}

#bg-switcher {
  position: fixed;
  top: 3rem;
  right: 0;
  width: 60px;
  height: 60px;
  background: red;
  z-index: 10;
  cursor: pointer;
}

.btn-kervan {
  min-width: 10rem;
  background-color: #ff6f55;
  color: #ffffff;
  font-size: 16px;
  font-weight: 300;
  padding: .5rem 1.5rem;
  border-radius: 500rem;
  text-decoration: none;
  transition: all .3s ease;
  text-align: center;
  display: inline-block;

  &:focus {
    outline: 0;
    box-shadow: 0 0 0 0;
  }

  &:hover {
    box-shadow: 0px 3px 20px #FF6F554D;
    color: white;
    text-decoration: none;

    .btn-arr {
      opacity: 0;
    }
  }

  &.btn-arr {
    position: relative;
    display: flex;
    align-items: center;
    padding-right: 3rem;

    &:after {
      position: absolute;
      display: block;
      content: '';
      right: 1.5rem;
      width: 9px;
      height: 14px;
      background: url("../img/btn-arr.png") no-repeat right;
      background-size: cover;
    }
  }

  &.without-bg {
    border: 1px solid #ff6f55;
    background: transparent;
    color: #ff6f55;

    &:hover {
      color: #ff6f55;
      border: 1px solid inherit;
    }

    &-arr {
      position: relative;
      display: flex;
      align-items: center;
      padding-right: 3rem;

      &:after {
        position: absolute;
        display: block;
        content: '';
        right: 1.5rem;
        width: 9px;
        height: 14px;
        background: url("../img/right-arr-red.png") no-repeat right;
        background-size: cover;
      }
    }
  }

  &.longer {
    padding: .5rem 4rem;
  }
}

.btn-yellow {
  background-color: #feb627;

  &:hover {
    box-shadow: 0px 3px 20px rgba(254, 182, 39, 0.3);
    color: white;
  }
}

.form-row > .col, .form-row > [class*="col-"] {
  padding-left: 7.5px;
  padding-right: 7.5px;
}

.form-row {
  margin-left: -7.5px;
  margin-right: -7.5px;

  label {
    margin-bottom: 0;
  }

  span {
    display: block;
    padding-bottom: rem(8);
    color: #7D8B9D;
    font-size: .75rem;
    font-weight: 500;
  }
}

.form-control, .form-control-sm, .form-control-lg {
  max-width: 100%;
  width: 100%;
  border: 1px solid #d3d9de;
  background-color: #fff;
  border-radius: 25rem;
  color: #000;
  font-weight: 500;
  text-indent: 0.75rem;

  &:focus {
    color: #495057;
    background-color: #fff;
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .25);
  }

  &:disabled {
    background: #fafafa;
  }
  @include lg-block(){
    text-indent: 0;
  }
}

.form-control {
  height: 2.625rem;
}

.form-control-sm {
  height: rem(32);
}

.form-control-lg {
  height: rem(50);
}

select.form-control {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-image: url("../img/arrow-down.png");
  background-repeat: no-repeat, repeat;
  background-position: right 1rem top 50%, 0 0;
  cursor: pointer;
  text-indent: 0.25rem;
}

textarea.form-control {
  font-size: rem(12);
  border-radius: rem(16);
  min-height: rem(80);
  padding: rem(10) rem(20);
  text-indent: 0;
}

.vertical-line {
  width: 100%;
  margin: .75rem 0;
  background: #e2e2e2;
  height: 1px;
}

.favourite-btn {
  font-size: 20px;
  color: black;

  &.active {
    color: #ff6f55;
  }
}

// sidebar nav

.sidebar-nav {
  &-items {
    padding: 2rem 1rem 0 1.5rem;
    user-select: none;
  }

  &-item {
    padding-bottom: 1.25rem;

    .icon-deal,
    .icon-email,
    .icon-sign-out {
      font-size: rem(13);
    }

    a {
      display: flex;
      flex-direction: row;
      align-items: center;
      text-decoration: none;

      span {
        width: rem(20);

        &:before {
          color: #A1ABB8;
        }
      }

      p {
        font-size: rem(14);
        font-weight: 400;
        padding-left: rem(14);
        margin: 0;
        color: #A1ABB8;
      }

      &:hover {
        p, span:before {
          color: #ff6f55;
        }
      }

      &.active {
        p, span:before {
          color: #ff6f55;
        }
      }

      &.disabled {
        cursor: default;

        p, span:before {
          color: #dfe3eb;
        }
      }
    }
  }
}

//End sidebar nav

//STARS
.star-rating__stars {
  position: relative;
  background: url("../img/star-not-checked.png");
  height: 15px;
  width: 80px;
  background-size: 16px 15px;

}

.star-rating__label {
  position: absolute;
  height: 100%;
  background-size: 16px 15px !important;
  cursor: pointer;
}

.star-rating__input {
  margin: 0;
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
}

.star-rating__stars .star-rating__label:nth-of-type(1) {
  z-index: 5;
  width: 20%;
}

.star-rating__stars .star-rating__label:nth-of-type(2) {
  z-index: 4;
  width: 40%;
}

.star-rating__stars .star-rating__label:nth-of-type(3) {
  z-index: 3;
  width: 60%;
}

.star-rating__stars .star-rating__label:nth-of-type(4) {
  z-index: 2;
  width: 80%;
}

.star-rating__stars .star-rating__label:nth-of-type(5) {
  z-index: 1;
  width: 100%;
}

.star-rating__input:checked + .star-rating__label,
.star-rating__input:focus + .star-rating__label,
.star-rating__label:hover {
  background: url("../img/star-checked.png");
}

.star-rating__label:hover ~ .star-rating__label {
  background: url("../img/star-not-checked.png");
}

.star-rating__input:focus ~ .star-rating__focus {
  position: absolute;
  top: -.25em;
  right: -.25em;
  bottom: -.25em;
  left: -.25em;
  outline: none;
}

//END_STARS


//Rulse for tags
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  outline: 0;
}

button {
  border: 0;
}

legend {
  color: #000;
}


a, button {
  cursor: pointer;
}

main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;

  &:hover {
    color: #000000;
  }
}

b, strong {
  font-weight: 600;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
